<script>
export default {
    name: "Tab",
    props: {
        label: {
            type: String,
            default: "tab",
        },
        index: {
            type: [String, Number],
            default: 0,
        },
    },
    computed: {
        isActive() {
            return this.$parent.currentIndex === this.index;
        }
    },
    mounted(){
      this.$parent.pans.push(this)
    },
    methods:{
      clickHandle(){
        this.$parent.getIndex(this.index)
      }
    },
    render() {
        let cureentClass = {
          tab:true,
          active:this.isActive
        }
        return <li onClick={ this.clickHandle } class={ cureentClass }>{this.label}</li>;
    },
};
</script>
<style scoped>

.tab {
    flex: 1;
    text-align: center;
    list-style: none;
    line-height: 0.4rem;
    position: relative;
    box-shadow: 0 0.06rem 0.25rem 0 rgba(229,239,255,0.9);
}

.tab.active {
    font-size: 0.17rem;
    color: #4169e2;
    border-radius: 3px;
    border-bottom: 2px solid #4169e2;
}
</style>