import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'


import APlayer from '@moefe/vue-aplayer'
Vue.use(APlayer, {
  defaultCover: '../assets/img/bbg.png',
  productionTip: true,

});
/* 引入并使用 vue-video-player */
import VideoPlayer from 'vue-video-player'
import 'vue-video-player/src/custom-theme.css'
import 'video.js/dist/video-js.css'
Vue.use(VideoPlayer)

import VueTouch from 'vue-touch'
Vue.use(VueTouch, {name: 'v-touch'})

Vue.use(VueRouter)


const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },

  {
    path: "/city/:cityName",
    name: "City",
    component: () =>
        import ("../views/City.vue")
},
{
    path: "/fyResult/:citys",
    name: "fyResult",
    component: () =>
        import ("../views/fyResult.vue"),
    props: true
},
  {
    path:'/Dtu',
    name: 'Dtu',
    component: () => import("../views/Dtu.vue")
  },
  {
    path:'/Newss',
    component: () => import("../views/Newss.vue")
  },
  {
    path:'/My',
    name: 'My',
    component: () => import("../views/My.vue")
  },
{
  path:'/SpringFestival',
  name:'SpringFestival',
  component: () => import("../components/SpringFestival.vue")
},
  {
    path:'/Tianqi',
    name: 'Tianqi',
    component: () => import("../components/Tianqi.vue")
  },



 
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

//顶部加载进度条效果使用
router.beforeEach((to, from, next) => {
  NProgress.start()
  next()
})
router.afterEach(() => {
  NProgress.done()
})
//控制速度，一般不用特意控制，进度条速度随网页加载速度
NProgress.inc(0.2)
NProgress.configure({ easing: 'ease', speed: 500, showSpinner: false })

export default router
