import axios from "../utils/request"
import base from './base'

const api = {
    /**
     * 获取疫情信息
     */
    getCovid19Info(params) {
        return axios.get( "http://api.tianapi.com" + "/txapi/ncov/index", {
            params
        })
    },
    /**
     * 省数据
     */
    getAllCity() {
        return axios.get(base.baseUrl + base.all)
    },

    /**
     * 国外疫情数据
     */
    // getNcovabroad() {
    //     return axios.get("http://api.tianapi.com" + "/ncovabroad/index?key=d9ad4134d29032a99bb258306c1af7f1")
    // },
    /**
     * 获取疫情图片曲线
     */
    getNocvImg() {
        return axios.get(base.baseUrl + base.ncovimg)
    },
    /**
     * 获取城市信息
     */
    getCitys(params) {
        return axios.get(base.baseUrl + base.citys, {
            params
        })
    },
    /**
     * 查询城市疫情信息
     */
    query(params) {
        return axios.get(base.baseUrl + base.query, {
            params
        })
    }
};

export default api;