<template>

    <div class="spring">
        <img src="../assets/img/zccx.png" alt="">
     <div class="bfzhuti">
   <img class="laba" src="../assets/img/laba.png"/>
   <Roll :lists="lists"></Roll>
 </div>
        <h3 class="title">疫情期间出行防疫政策</h3>
        <MyCascader label="出发城市" :options="options" @onCitys="getFromCitys"/>
        <MyCascader label="到达城市" :options="options" @onCitys="getToCitys"/>
        <van-button class="selectBtn" type="primary" block @click="springHandle">点击查询</van-button>
    </div>
</template>

<script>
import api from "../api";
import MyCascader from "./MyCascader"

export default {
    data() {
        return {
            options:[],
            from:{},
            to:{},
            lists: 
        [
            '温馨提示：',
            '旅行期间，尽量避免到人群聚集、空间密闭、通风较差场所活动。持续做好自我健康监测，出现可疑症状时，及时就诊，并做好防范感染他人的措施。'
        ]
     };
    },
    components:{
        MyCascader
    },
    mounted() {
        api.getCitys({
            key: "171e165a7d991c5f6ecd5194c54778ef"
        })
            .then(res => {
                if (!res.data.error_code) {
                    var citys = [];
                    for (var i = 0; i < res.data.result.length; i++) {
                        var currentCitys = [];
                        for(var j = 0;j<res.data.result[i].citys.length;j++){
                            var tempChild = {
                                text:res.data.result[i].citys[j].city,
                                value:res.data.result[i].citys[j].city_id
                            }
                            currentCitys.push(tempChild)
                        }
                        var temp = {
                            text:res.data.result[i].province,
                            value:res.data.result[i].province_id,
                            children:currentCitys
                        }
                        citys.push(temp)
                    }
                    this.options = citys;
                }
            })
            .catch(error => {
                console.log(error);
            });
    },
    methods:{
        getFromCitys(city){
            this.from = city;
        },
        getToCitys(city){
            this.to = city;
        },
        springHandle(){
          this.$router.push({ name:"fyResult",params:{citys:{from:this.from,to:this.to}} })
          this.$notify({type: "danger", message: '请选择城市' })
        }
    }
    
};
</script>

<style scoped lang="less">
.spring {
    position: relative;
    background: #fff;
    box-sizing: border-box;
    .title {
        text-align: center;
        font-size: 0.16rem;
    }
}

.spring img{
    width: 100%;
}
.bfzhuti{
    font-size:21/75rem;
    color:#525252;
    display: flex;
    align-items: center;
    .laba {
      width: 21/75rem;
      height: 21/75rem;
    }
}
</style>