<template>
  <div class="chart">
    <p class="title"><i></i>全国疫情趋势</p>
    <div class="qsbox">
        <ul class="navigator">
          <li
            @click="changePage(index)"
            :class="{ active:index === currentIndex }"
            class="navigatorItem"
            v-for="(item,index) in quanguoTrendChart"
            :key="index"
          >{{ item.title }}</li>
      </ul>
      <swiper ref="mySwiper" :options="swiperOption">
          <swiper-slide v-for="(slide,index) in quanguoTrendChart" :key="index">
              <img :src="slide.imgUrl" alt="">
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
    data(){
        const vm = this;
        return{
            currentIndex:0,
            swiperOption:{
                on:{
                    slideChangeTransitionEnd(){
                        vm.currentIndex = this.activeIndex
                    }
                }
            },
            quanguoTrendChart:[
                 {
                    imgUrl:
                        "https://img1.dxycdn.com/2020/0220/014/3397684583507458039-135.png",
                    title: "新增疑似&确诊"
                },
                {
                    imgUrl:
                        "https://img1.dxycdn.com/2020/0220/697/3397684611424536901-135.png",
                    title: "现存确诊&疑似"
                },
                {
                    imgUrl:
                        "https://img1.dxycdn.com/2020/0220/861/3397684624309439853-135.png",
                    title: "死亡/治愈"
                },
                {
                    imgUrl:
                        "https://img1.dxycdn.com/2020/0220/168/3397686703073768694-135.png",
                    title: "病死率"
                },
                {
                    imgUrl:
                        "https://img1.dxycdn.com/2020/0220/709/3397686724548816431-135.png",
                    title: "治愈率"
                }
            ]
        }
    },
    mounted(){

    },
    methods:{
        changePage(index){
            this.currentIndex = index  // 高亮
            this.$refs.mySwiper.swiper.slideTo(index,1000,false)  // swiper切换
        }
    }
}
</script>

<style scoped>

.chart {
    clear: both;
    position: relative;
    background: #fff;
    padding: 0.16rem;}
.qsbox{
    border: 0.01rem solid #ebebeb;
    border-radius: 0.08rem;
    box-shadow: 0 0.04rem 0.12rem 0 rgba(0, 0, 0, 0.05);
    z-index: 0;
}
.title {
    
    font-size: 0.17rem;
}

.title i {
    display: inline-block;
    width: 0.05rem;
    height: 0.17rem;
     margin: 0 .03rem 0 0;
    vertical-align: middle;
    background: #4169e2;
}
.chart .swiper-pagination {
    position: absolute;
    text-align: center;
    -webkit-transition: 300ms opacity;
    -o-transition: 300ms opacity;
    transition: 300ms opacity;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    z-index: 10;
}
.chart .swiper-pagination-bullet {
    width: calc(20% - 0.02rem);
    text-align: center;
    background: #f7f7f7;
    padding: 0.045rem;
    box-sizing: border-box;
    color: #666;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.02rem;
}
.navigator {
    font-size: 0.12rem;
    list-style: none;
    display: flex;
    padding: 0 0.05rem;
    justify-content: center;
    margin: 0.06rem 0 0;
}
.navigatorItem {
    color: #4169e2;
    background: #f1f5ff;
    position: relative;
    width: calc(20% - 0.02rem);
    text-align: center;
    background: #f7f7f7;
    padding: 0.045rem;
    box-sizing: border-box;
    color: #666;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.02rem;
    margin-left: 0.025rem;
}
.active {
    color: #4169e2;
    background: #f1f5ff;
    position: relative;
}
swiper-wrapper{
    height: 100%;
    margin: 0 auto;
}
.swiper-container {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    overflow: hidden;
    list-style: none;
    padding: 0;
    z-index: 0;
}
</style>