import echarts from "echarts"
import Vue from "vue"
import GWname from "./gwname"

const install = function () {

    Object.defineProperties(Vue.prototype, {
        $charts: {
            get() {
                return {
                    chinaMap(id, data) {
                        var chartDom = document.getElementById(id);
                        var myChart = echarts.init(chartDom);
                        var option = {
                            tooltip: {
                                showDelay: 0,
                                transitionDuration: 0.2,
                                triggerOn: "click",
                                enterable: true,
                                formatter(data) {
                                    return "<div><p>" + data.name + "</p><p>现有诊人数:" + data.value + "</p></div>"
                                }
                            },

                            title: {
								fontFamily: '微软雅黑',
								top: 3,
                                text: "中国地图",
                       
                                subtext: "共抗疫情,爱不缺席！",
                              
                                x: 'center',

                            },
							
                            toolbox: {
                                feature: {
                                    saveAsImage: {}
                                }
                            },

                            visualMap: [{
                                orient: "vertical",
                                type: "piecewise",
                                pieces: [{
                                        min: 0,
                                        max: 0,
                                        color: "rgb(197,240,253)"
                                    },
                                    {
                                        min: 1,
                                        max: 10,
                                        color: "rgb(58,186,235)"
                                    },
                                    {
                                        min: 10,
                                        max: 100,
                                        color: "rgb(31,109,211)"
                                    },
                                    {
                                        min: 100,
                                        max: 500,
                                        color: "rgb(13,83,163)"
                                    },
                                    {
                                        min: 500,
                                        max: 10000,
                                        color: "rgb(0,1,158)"
                                    }
                                ]
                            }],
                            series: [{
                                name: "中国地图",
                                type: "map",
                                map: "china",
                                roam: true,
                                zoom: 1.3,
                                label: {
                                    normal: {
                                        show: true,
                                        textStyle: {
                                            fontSize: 10,
                                        }
                                    }
                                },
                                itemStyle: {
                                    normal: {
                                        areaColor: 'rgba(0,255,236,0)',
                                        borderColor: 'rgba(0,0,0,0.2)',
                                    },
                                    emphasis: {
                                        areaColor: 'rgb(125,158,255)',
                                        shadowOffsetX: 0,
                                        shadowOffsetY: 0,
                                        shadowBlur: 20,
                                        borderWidth: 0,
                                    }
                                },
                                data
                            }]
                        }
                        myChart.setOption(option);
                    },
                    worldMap(id, data) {
                        var chartDom = document.getElementById(id);
                        var myChart = echarts.init(chartDom);
                        var option = {
							title: {
								fontFamily: '微软雅黑',
								top: 3,
                                text: "世界地图",
                               
                                subtext: "共抗疫情,爱不缺席！",
                           
                                x: 'center',

                            },
                            tooltip: {
                                triggerOn: "click",
                                enterable: true,
                                formatter(data) {
                                    return "<div><p >" + data.name + "</p><p>现有诊人数:" + data.value + "</p></div>"
                                }
                            },
                            visualMap: [{
                                orient: "vertical",
                                type: "piecewise",
                                pieces: [{
                                        min: 0,
                                        max: 0,
                                        color: "rgb(197,240,253)"
                                    },
                                    {
                                        min: 1,
                                        max: 1000,
                                        color: "rgb(58,186,235)"
                                    },
                                    {
                                        min: 1000,
                                        max: 10000,
                                        color: "rgb(31,109,211)"
                                    },
                                    {
                                        min: 10000,
                                        max: 100000,
                                        color: "rgb(13,83,163)"
                                    },
                                    {
                                        min: 100000,
                                        max: 10000000,
                                        color: "rgb(0,1,158)"
                                    }
                                ]
                            }],
                            series: [{
                                name: "世界地图",
                                type: "map",
                                map: "world",
                                roam: true,
                                zoom: 1.3,
                                label: {
                                    normal: {
                                        show: false,
                                        textStyle: {
                                            fontSize: 10,
                                        }
                                    }
                                },
                                nameMap: GWname,
                                itemStyle: {
                                    normal: {
                                        areaColor: 'rgba(0,255,236,0)',
                                        borderColor: 'rgba(0,0,0,0.2)',
                                    },
                                    emphasis: {
                                        areaColor: 'rgba(125,158,255)',
                                        shadowOffsetX: 0,
                                        shadowOffsetY: 0,
                                        shadowBlur: 20,
                                        borderWidth: 0,
                                    }
                                },
                                data
                            }]
                        }
                        myChart.setOption(option);
                    }
                }
            }
        }
    })
}

export default install